@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.btn-primary{background:#18a0fb;color:#ffffff;border-radius:5px;font-weight:500;font-size:13px;line-height:16px;text-align:center}.btn-primary:focus,.btn-primary:active{outline:none}.btn-primary_small{padding:10px 12px;border:0;font-family:"Inter", sans-serif;display:inline-block;cursor:pointer}.btn-primary__icon{display:inline-block;width:20px;margin-left:10px}.btn-primary:disabled{background-color:#E0E0E0;cursor:not-allowed}a.btn-primary{text-decoration:none}.disabled{background-color:#E0E0E0;cursor:not-allowed}.btn-primary:active{background:#0489e2}.btn-primary_small.active{background:#0489e2}

*{margin:0;font-family:"Inter", sans-serif}

.sandbox-block{padding:1rem}.sandbox-block__element{padding:1rem 0}.sandbox-block__header{padding:0.5rem 0}h1,h1,span{font-weight:400}

.navigation{background-color:#18a0fb;padding:1rem}.navigation__list{display:flex;list-style-type:none;-webkit-padding-start:0;padding-inline-start:0}.navigation__li{padding-right:1rem}.navigation__li a{color:white;text-decoration:none}.navigation__li a:active,.navigation__li a:hover,.navigation__li a:focus{color:#ececec;text-decoration:none}

