.btn-primary {
  background: #18a0fb;
  color: #ffffff;
  border-radius: 5px;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
}

.btn-primary:focus, .btn-primary:active  {
  outline: none;
}
.btn-primary_small {
  padding: 10px 12px;
  border: 0;
  font-family: "Inter", sans-serif;
  display: inline-block;
  cursor: pointer;
}
.btn-primary__icon {
  display: inline-block;
  width: 20px;
  margin-left:10px;
}
.btn-primary:disabled {
  background-color: #E0E0E0;
  cursor: not-allowed;
}
a.btn-primary{
  text-decoration: none;
}
.disabled {
  background-color: #E0E0E0;
  cursor: not-allowed;
}

.btn-primary:active {
  background: #0489e2;
}
.btn-primary_small.active {
  background: #0489e2;
}
